import "./contact.scss";
import React from 'react';
import Fade from 'react-reveal/Fade';

class Contact extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            subject: '',
            message: ''
        };

        this.handleChangeName = this.handleChangeName.bind(this);
        this.handleChangeEmail = this.handleChangeEmail.bind(this);
        this.handleChangeSubject = this.handleChangeSubject.bind(this);
        this.handleChangeMessage = this.handleChangeMessage.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    
    handleChangeName(event) {
        this.setState({name: event.target.value});
    }

    handleChangeEmail(event) {
        this.setState({email: event.target.value});
    }

    handleChangeSubject(event) {
        this.setState({subject: event.target.value});
    }

    handleChangeMessage(event) {
        this.setState({message: event.target.value});
    }
    
    handleSubmit(event) {
        if(this.state.name == '' || this.state.email == '' || this.state.message == ''){
            alert("Attention! Fill in the contact fields correctly.");
            event.preventDefault();
            return;
        }
        alert("Your message has been sent! I will reply as soon as possible.");
        event.preventDefault();
        this.setState({name: '', email: '', subject: '', message: ''});

        //send message on Telegram group
        this.send();
    }

    send(){
        console.log("https://api.telegram.org/bot2126273165:AAF3JH8x3SkXBPUKnqy9ukg7Gi_ZcErOUic/sendMessage?chat_id=-1001790348625&text=" + "Name: " + this.state.name + " - Email: " 
                + this.state.email + " - Subject: " + this.state.subject
                + " - Message: " + this.state.message);
        
        //fetch result
        fetch("https://api.telegram.org/bot2126273165:AAF3JH8x3SkXBPUKnqy9ukg7Gi_ZcErOUic/sendMessage?chat_id=-1001790348625&text=" + "Name: " + this.state.name + " - Email: " 
            + this.state.email + " - Subject: " + this.state.subject
            + " - Message: " + this.state.message, {
            method: "GET",
            headers: {
                "Content-Type": "text/plain"
            }
        }).then(function(response) {
            console.log(response);
        })
        .catch((error) => {
          console.log(error)
          alert("An error has occurred! Please try again ...");
        });
    }
    
    render() {
        return (
            <div className="contact" id="contact">
                <Fade left>
                    <div className="left">
                        <img src="assets/contact.svg" alt="" />
                    </div>
                </Fade>
                <Fade right>
                    <div className="rigth">
                        <h1>Contact me</h1>
                        <form onSubmit={this.handleSubmit}>
                            <label> Your name:</label>
                            <input value={this.state.name} onChange={this.handleChangeName}/>
                            <br />
                            <label> Your email:</label>
                            <input type="email" value={this.state.email} onChange={this.handleChangeEmail}/>
                            <br />
                            <label> Subject:</label>
                            <input value={this.state.subject} onChange={this.handleChangeSubject} />
                            <br />
                            <label> Your message:</label>
                            <textarea value={this.state.message} onChange={this.handleChangeMessage}/>
                            <br />
                            <input type="submit" value="Submit" />
                        </form>
                    </div>
                </Fade>
            </div>
    )};
}

export default Contact;