import "./about.scss"
import ProgressBar from "@ramonak/react-progress-bar";
import {language} from "../../data";
import Fade from 'react-reveal/Fade';

export default function About() {
    return (
        <div className="about" id="about">
            <Fade bottom>
            <div className="left">
                <div className="textContainer">
                    <h1>About.</h1> 
                    <h2>I am a guy who likes to improve efficiency in people's lives.</h2>
                    <br/><br/>
                    <h3>I enjoy collaborating with people to develop innovative ideas.
                    If I'm not writing code, doing calisthenics, reading books or spending time with people who are important to me.</h3>
                </div>
            </div>
            </Fade>
            <Fade right cascade>
            <div className="right">
                <div className="skills">
                    <div className="title">My skills</div>
                    {language.map((l) => (
                        <div className="item" >
                            <h3>{l.name}</h3>
                            <ProgressBar className="progressBar" completed={l.value} maxCompleted={100} customLabel=""  labelSize="0px"
                                bgColor="red" height="4px" animateOnRender={true} transitionDuration="2s"/>
                        </div>
                    ))}
                </div>
            </div>
            </Fade>
        </div>
    )
}
