import "./quotes.scss"
import React from 'react'
import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';

export default function Quotes() {

    const quotes = [
        {
            author: "Thomas Jefferson",
            quote: "If you want something you've never had, you have to do something you've never done.",
        },
        {
            author: "Steve Jobs",
            quote: "Stay hungry. Stay foolish.",
        },
        {
            author: "Elon Musk",
            quote: "You should take the approach that you’re wrong. Your goal is to be less wrong.",
        },
        {
            author: "Benjamin Franklin",
            quote: "Tell me and I forget. Teach me and I remember. Involve me and I learn.",
        },
    ];

    const properties = {
        duration: 2500,
        arrows: false,
        autoplay: true,
        indicators: i => (<div className="dot"></div>),
      };

    return (
        <div className="quotes">
            <div className="slideShow">
                <Slide {...properties}>
                    {quotes.map((q, index) => (
                        <div className="item-slide" key={index}>
                            <div className="container">
                                <img src="assets/quotes.png" alt="" />
                                <div className="content">
                                    <div className="quote">{q.quote}</div>
                                    <div className="author">{q.author}</div>
                                </div>
                            </div>
                        </div>
                    ))}
                </Slide>
            </div>
        </div>
    )
}
