import "./university.scss"
import { course } from "../../data"

export default function University() {
    return (
        <div className="university" id="university">
            <div className="title">
                University
            </div>
            <div className="description">
                <div className="text">
                I am a university student. I started my study path in 2019, at the University of 
                Parma with the "Ingegneria dei sistemi informativi" course. This journey will end
                in the summer of 2022, with my graduation. Below you will find the marks of the 
                exams I have taken so far.
                </div>
            </div>
            <div className="cards-container">
                {course.map((c) => <div className="card">
                    <div className="courseName">
                        {c.name}
                    </div>
                    <div className="mark">
                        {c.mark}
                    </div>
                </div>
                )}
            </div>
        </div>
    )
}
