import "./experiences.scss";

export default function Experiences() {
 
    return (
        <div className="experiences" id="experiences">
            <div className="title">My experiences</div>
            <div className="content">
                <div className="item">
                    <div className="text">
                        <div className="top">
                            2019
                        </div>
                        <div className="bottom">
                            My first experience with programming was at the age of 17 when I attended a college internship where 
                            I developed the Space Invaders video game with Python.
                        </div>
                    </div>
                    <img src="./assets/space-invaders.png" alt="" />
                </div>
                <div className="item">
                    <img src="./assets/img-developer.png" alt="" />
                    <div className="text">
                        <div className="top">
                            2019-2021
                        </div>
                        <div className="bottom">
                            I worked as an industrial PLC programmer using professional IDEs like SoMachineExpert and Zelio Soft.
                        </div>
                    </div>
                </div>
                <div className="item">
                <div className="text">
                        <div className="top">
                            2021
                        </div>
                        <div className="bottom">
                            After participating in a mobile development course, I became passionate about the sector and decided to 
                            undertake a path as an Android developer 
                            (<a href="https://play.google.com/store/apps/dev?id=7631347723206063427" target="_blank">MobDevFC</a>).
                        </div>
                    </div>
                    <img src="./assets/mobdevfc.png" alt="" />
                </div>
                <div className="working-in-progress">
                    <div className="text">
                        Work in progress...
                    </div>
                    <img src="./assets/work-in-progress.png" alt="" />
                </div>
            </div>
        </div>
    )
}
